import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
const moment = require('moment');

Vue.use(Vuex);

export default new Vuex.Store({

    state: {
        query: '',
        title: '',
        results: [],
        tvInfo: {},
        epInfo: {},
        showDetailsBox: false,
        shResults: false,
        currentDate: moment().format('YYYY-MM-DD'),
        imgClass: 'bgImage'
    },

    getters: {

    },

    mutations: {

        SEARCH(state, results) {
            state.results = results;
            state.shResults = true;
        },

        SHOW_INFO(state, info) {
            state.query = info.name;
            state.title = info.name;
            if(info.network === null) {
                state.tvInfo.studio = '';
            } else {
                state.tvInfo.studio = info.network.name;
            }
            state.tvInfo.poster = info.image.medium;
        },

        SHOW_DETAILS(state, show) {
            
            let lastEp = show.filter(latest => {
                if(moment(latest.airdate).isBefore(state.currentDate)) {
                  return true;
                }
              });
              let episode = lastEp.slice(-1)[0];
              state.imgClass = 'bgImage';
              state.showDetailsBox = true;
              state.shResults = false;
              state.epInfo = {
                season: episode.season,
                episode: episode.number,
                title: episode.name,
                summary: episode.summary,
                pic: episode.image.original,
                aired: moment(episode.airdate).format('MMMM Do, YYYY')
              }
        },

        RESET(state) {
            state.tvInfo = {};
            state.epInfo = {};
            state.showDetailsBox = false;
        }

    },

    actions: {

        search({state, commit}, query) {
            return axios({
                method: 'get',
                url: 'http://api.tvmaze.com/search/shows',
                params: {
                    q: query,
                }
            })
            .then(res => commit('SEARCH', res.data))
            .catch(err => console.log(err));
        },

        getDetails({dispatch, state, commit}, id) {
            axios.all([
                axios.get('http://api.tvmaze.com/shows/'+ id +'/episodes'),
                axios.get('http://api.tvmaze.com/shows/'+ id)
              ])
              .then(axios.spread(function (details, info) {
                commit('SHOW_DETAILS', details.data);
                commit('SHOW_INFO', info.data);
              }));
            return axios({
                method: 'get',
                url: 'http://api.tvmaze.com/shows/'+ id +'/episodes'
            })
            .catch(err => console.log(err)); 
          }

    }

})


