<template>
    <section v-if="showDetailsBox">
      <div class="posterbox">
          <img id="poster" :src="`${tvInfo.poster}`">
          <h3 id="studio">{{ tvInfo.studio }}</h3>
      </div>
      <div class="infobox">
        <h1 id="title">{{ title }}</h1>
        <ul id="stats">
            <li><strong>Season:</strong> {{ epInfo.season }}</li>
            <li><strong>Epidode:</strong> {{ epInfo.episode }}</li>
            <li><strong>Title:</strong> {{ epInfo.title }}</li>
            <li><strong>Aired On:</strong> {{ epInfo.aired }}</li>
        </ul>
        <span v-html="limitSummary(epInfo.summary)" id="summary"></span>
      </div>
      <div id="bkg" class="bgImage">
          <img id="img">
      </div>
    </section>
</template>

<script>

    export default {

        data() {
            return {
                
            }
        },

        computed: {
            tvInfo() {
                return this.$store.state.tvInfo;
            },
            epInfo() {
                return this.$store.state.epInfo;
            },
            title() {
                return this.$store.state.title;
            },
            showDetailsBox() {
                return this.$store.state.showDetailsBox;
            }
        },

        methods: {
            limitSummary(value) {
                if(!value) return '';
                return value.split(' ').splice(0,40).join(' ') + '...';
            }
        },

        mounted() {
            const bkg = document.getElementById('bkg');
            const poster = document.getElementById('poster');
            const studio = document.getElementById('studio');
            const title = document.getElementById('title');
            const stats = document.getElementById('stats');
            const summary = document.querySelector('#summary p');
        },

        updated() {
            const downloadingImage = new Image();
            const tl = new TimelineLite();

            downloadingImage.onload = function(){
                img.src = this.src;
                if(img.complete) {
                    tl
                    .from(bkg, 0.5, {autoAlpha: 0})
                    .from(poster, 0.2, {y: 20, autoAlpha: 0})
                    .from(studio, 0.2, {y: 20, autoAlpha: 0})
                    .from(title, 0.2, {y: 20, autoAlpha: 0})
                    .from(stats, 0.2, {y: 20, autoAlpha: 0})
                    .from(summary, 0.2, {y: 20, autoAlpha: 0});
                }
                
            };
            downloadingImage.src = this.$store.state.epInfo.pic;
        }

    }
</script>


