<template>
    <div>
        <input type="text" 
            placeholder="Your favourite TV Show" 
            v-model="query" 
            @input="search($event.target.value)" 
            @keydown="itemSelect($event)"
            @focus="reset($event)"
        >

        <ul class="results" v-if="shResults">
            <li v-for="(item, index) in results" 
            :class="{'highlighted': index === cursorPos}"
                :key="item.show.id" 
                @click="getDetails(item.show.id)">
                {{ item.show.name }}
            </li>
        </ul>
    </div>
</template>

<script>

    import { Observable } from 'rxjs/Observable';
    import 'rxjs/add/observable/from';
    import 'rxjs/add/operator/filter';
    import 'rxjs/add/operator/debounceTime';
    import 'rxjs/add/operator/distinctUntilChanged';

    export default {

        data() {
            return {
                cursorPos: -1
            }
        },

        computed: {
            query: {
                get() {
                    return this.$store.state.query;
                },
                set(value) {
                    this.$store.state.query = value;
                }
            },
            results() {
                return this.$store.state.results;
            },
            shResults() {
                return this.$store.state.shResults;
            }
        },

        methods: {

            search(search) {
                let obs = Observable.create((observer) => {
                    observer.next(search);
                });
                obs
                    .filter(text => text.length > 2)
                    .debounceTime(500)
                    .distinctUntilChanged()
                    .subscribe(query => {
                        this.$store.dispatch('search', query);
                    });
            },

            getDetails(id) {
                this.$store.dispatch('getDetails', id);
            },

            itemSelect(event) {
                switch(event.keyCode) {
                    case 38:
                        if(this.cursorPos <= 0) {
                            this.cursorPos = this.results.length;
                        } else {
                            this.cursorPos -= 1;
                        }
                        break;
                    case 40:
                        if(this.cursorPos === (this.results.length - 1)) {
                            this.cursorPos = 0;
                        } else {
                            this.cursorPos += 1;
                        }
                        break;
                    case 13: 
                        let id = this.results[this.cursorPos].show.id;
                        this.getDetails(id);
                        break;
                }
            },

            reset(event) {
                event.target.value = '';
            }
        }

    }
</script>


